.button--transparente,.button--twitter,.button--google{display:flex;align-items:center;justify-content:center}.input--login,.button--transparente,.button--twitter,.button--google{font-family:"Muli",sans-serif;font-size:16px;color:#fff;height:50px}.input--login{border:2px solid #fff;padding:0px 20px;margin:auto;display:flex;justify-content:center}.button--transparente,.button--twitter,.button--google{font-weight:bold;cursor:pointer;border:none;border-radius:10px;padding:5px 40px;margin:10px 0px}.login{color:#fff;display:flex;flex-direction:column;align-items:center;justify-content:center;padding:30px 30px;min-height:calc(100vh - 120px)}.login__container{background-color:rgba(255,255,255,0.1);border:2px solid #fff;border-radius:40px;padding:60px 68px;min-height:700px;width:300px;display:flex;justify-content:space-around;flex-direction:column}@media only screen and (max-width: 600px){.login__container{background-color:transparent;border:none;padding:0px;width:100%}}.login__form{display:flex;flex-direction:column}.login__remember-me{font-size:14px;display:flex;justify-content:space-between;margin-top:10px}.login__remember-me a{text-decoration:none;color:#fff}.login__remember-me a:hover{text-decoration:underline}.login__social-media{display:flex;flex-direction:column;justify-content:center}.login__register{font-size:14px}.login__register a{font-size:16px;font-weight:bold;text-decoration:none;color:#fff}.login__register a:hover{text-decoration:underline}.button--transparente{background-color:rgba(255,255,255,0.3);border-radius:25px;letter-spacing:1px}.button--twitter{background-color:#00acee}.button--google{background-color:#db4437}.button__icon{width:16px;margin-right:10px}.input--login{background-color:transparent;border-left:0px;border-right:0px;border-top:0px;margin:0px 0px 20px 0px;outline:none}::placeholder{color:#fff}
