.header__img--letras{font-family:"Muli",sans-serif;font-size:22px;color:#fff;font-weight:bold}.header__menu li a,.header__menu--profile p{font-family:"Muli",sans-serif;font-size:14px;color:#fff}.header{background-color:#ee2b47;width:100%;height:60px;display:flex;justify-content:space-between;align-items:center}.header__img a{display:flex;align-items:center;text-decoration:none}.header__img--logo{width:45px;margin-left:15px;margin-bottom:10px}.header__img--letras{align-items:center;margin-left:5px}.header__menu{margin-right:18px}.header__menu:hover ul,.header__menu ul:hover{display:block}.header__menu ul{display:none;list-style:none;padding:0px 18px 0px 10px;position:absolute;width:100px;text-align:right;margin:0px 0px 0px -13px;background-color:#ee2b47;border-radius:10%}.header__menu li{margin:10px 0px}.header__menu li a{text-decoration:none}.header__menu li a:hover{text-decoration:underline}.header__menu--profile{margin-right:8px;display:flex;align-items:center;cursor:pointer}.header__menu--profile img{margin-right:8px;width:50px;border-radius:100%;object-fit:cover}.header__menu--profile p{margin:0px}
