.input--register{font-family:"Muli",sans-serif;font-size:16px;color:#fff;height:50px}.input--register{border:2px solid #fff;padding:0px 20px;margin:auto;display:flex;justify-content:center}.register{color:#fff;display:flex;flex-direction:column;align-items:center;justify-content:center;padding:30px 30px;min-height:calc(100vh - 120px)}.register__container{background-color:rgba(255,255,255,0.1);border:2px solid #fff;border-radius:40px;padding:60px 68px;min-height:700px;width:300px;display:flex;justify-content:space-around;flex-direction:column}@media only screen and (max-width: 600px){.register__container{background-color:transparent;border:none;padding:0px;width:100%}}.register__form{display:flex;flex-direction:column}.register__init{display:flex;justify-content:center}.register__init a{font-size:16px;font-weight:bold;text-decoration:none;color:#fff}.register__init a:hover{text-decoration:underline}.input--register{background-color:transparent;border-left:0px;border-right:0px;border-top:0px;margin:0px 0px 20px 0px;outline:none}::placeholder{color:#fff}
