.carousel-item{background-color:turquoise;width:200px;height:300px;border-radius:20px;overflow:hidden;margin-right:10px;display:inline-block;cursor:pointer;transition:450ms all;transform-origin:center left;position:relative}.carousel-item:hover ~ .carousel-item{transform:translate3d(100px, 0, 0)}.carousel-item__img{width:200px;height:300px;object-fit:cover}.carousel-item__details{background:linear-gradient(to top, rgba(0,0,0,0.9) 0%, rgba(0,0,0,0) 100%);font-weight:bold;color:white;opacity:0;transition:450ms opacity;padding:10px;position:absolute;top:0;left:0;right:0;bottom:0;display:flex;flex-direction:column;justify-content:flex-end;align-items:flex-end}.carousel-item__details:hover{opacity:1}.carousel-item__details--img{margin:0px}.carousel-item__details--title{font-size:14px;margin:0px}.carousel-item__details--subtitle{font-size:12px;margin:0px}
