.button--back,.error-404{display:flex;align-items:center;justify-content:center}.error-404 p{font-family:"Muli",sans-serif;font-size:22px;color:#fff;font-weight:bold}.button--back{font-family:"Muli",sans-serif;font-size:16px;color:#fff;height:50px}.button--back{font-weight:bold;cursor:pointer;border:none;border-radius:10px;padding:5px 40px;margin:10px 0px}.error-404{min-height:calc(100vh - 60px - 60px);display:grid;grid-template-rows:10% 10% 10% 10% 10% 10% 10% 10% 10% 10%;grid-template-columns:10% 10% 10% 10% 10% 10% 10% 10% 10% 10%;margin:15px}.error-404__caja{grid-area:2 /3 / 4 / 6}.error-404 p{grid-area:4 / 3 / 6 / 6;justify-self:center;align-self:center}.error-404 img{grid-area:2 / 6 / 8 / 9;width:350px;justify-self:left}.error-404 .button--back{grid-area:6/3/8/6}@media only screen and (max-width: 600px){.error-404{display:flex;flex-direction:column}.error-404 img{width:200px}}.button--back{background-color:rgba(255,255,255,0.3);width:200px;justify-self:center}.text{text-align:center;margin-top:56px;color:#fff;font-size:50px;font-weight:bold}.animated{animation-duration:5.5s;animation-fill-mode:both;animation-iteration-count:infinite}@keyframes bounce{0%,20%,50%,80%,100%{transform:translateY(0)}30%{transform:translateY(-60px)}70%{transform:translateY(-15px)}}.bounce{animation-name:bounce}
