.carousel{width:100%;overflow-x:scroll}.carousel__container{white-space:nowrap;margin:70px 0px;padding:10px 30px}.carousel__container__container:hover .carousel-item{opacity:0.3}.carousel::-webkit-scrollbar{width:12px}.carousel::-webkit-scrollbar-track{-webkit-box-shadow:inset 0 0 6px rgba(0,0,0,0);box-shadow:inset 0 0 6px rgba(0,0,0,0);border-radius:10px}.carousel::-webkit-scrollbar-track:hover{-webkit-box-shadow:inset 0 0 6px #000;box-shadow:inset 0 0 6px #000;border-radius:10px}.carousel::-webkit-scrollbar-thumb{border-radius:10px;-webkit-box-shadow:inset 0 0 6px #000;box-shadow:inset 0 0 6px #000}.carousel__container:hover .carousel-item:hover{transform:scale(1.5);opacity:1}
